import React, { useState } from "react";
import { useDispatch } from "react-redux";
import { useAuth0 } from "@auth0/auth0-react";
import { useForm, Controller } from "react-hook-form";
import {
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Button,
  FormGroup,
  Label,
  Input,
  Row,
  Col
} from "reactstrap";
import DateTimePicker from "react-widgets/lib/DateTimePicker"

import { updateDefect } from "../../redux-store/defectSlice";

import AssignDevInput from "./AssignDevInput";

const EditDefectForm = ({ open, setOpen, defect, users, projectTitle }) => {
  const dispatch = useDispatch();
  const { getAccessTokenSilently } = useAuth0();
  const { register, errors, handleSubmit } = useForm();

  const [summary, setSummary] = useState(defect.summary);
  const [description, setDescription] = useState(defect.description);
  const [status, setStatus] = useState(defect.status);
  const [dev, setDev] = useState(defect.assignedDevId);
  const [priority, setPriority] = useState(defect.priority);
  const [dateIdentified, setDateIdentified] = useState(new Date(defect.identifierId));
  const [targetResDate, setTargetResDate] = useState(defect.targetResDate);
  const [actualResDate, setActualResDate] = useState(defect.actualResDate);
  const [progress, setProgress] = useState(defect.progress);

  // Also need to add developer, identifier information

  function handleClose() {
    const targetDate = defect.targetResDate ? defect.targetResDate : null;
    const actualDate = defect.actualResDate ? defect.actualResDate : null;

    setSummary(defect.summary);
    setDescription(defect.description);
    setStatus(defect.status);
    setDev(defect.assignedDevId);
    setPriority(defect.priority);
    setDateIdentified(new Date(defect.dateIdentified))

    setTargetResDate(targetDate);
    setActualResDate(actualDate);

    setOpen(false);
  }

  function onSubmit() {
    const devInput = dev ? dev.value : null;

    getAccessTokenSilently()
      .then(token => {
        const updateValues = {
          id: defect.id,
          token,
          summary,
          description,
          assignedDevId: devInput,
          status,
          priority,
          dateIdentified,
          targetResDate,
          actualResDate,
          progress
        };
        dispatch(updateDefect(updateValues));
      });
    setOpen(false);
  }

  function onDateChange(dateValue, callback) {
    console.log('dateValue', dateValue)
    if (!dateValue) {
      console.log('callback', callback)
      callback(null);
    }
    const dateObject = new Date(dateValue);
    console.log(" ~ file: EditDefectForm.js ~ line 87 ~ onDateChange ~ dateObject", dateObject)
    return callback(dateObject);
  }

  return (
    <Modal isOpen={open} toggle={handleClose} backdrop="static" size="xl">
      <form onSubmit={handleSubmit(onSubmit)}>
        <ModalHeader toggle={handleClose}>Edit Defect #{defect.id}</ModalHeader>

        <ModalBody>
          <Row>
            <Col md="12" lg="4">
              <FormGroup>
                <b><Label for="defectSummary">Summary</Label></b>
                <Input
                  type="text"
                  name="defectSummary"
                  id="defectSummary"
                  value={summary}
                  onChange={e => setSummary(e.target.value)}
                  innerRef={register({ required: true })}
                />
                <p style={{ color: "red" }}>{errors.defectSummary && "Summary is required."}</p>
              </FormGroup>
            </Col>

            <Col md="6" lg="4">
              <FormGroup>
                <b><Label for="identifier">Identifier</Label></b>
                <Input
                  type="text"
                  disabled
                  name="identifier"
                  id="identifier"
                  value={defect.identifierId}
                />
              </FormGroup>
            </Col>

            <Col md="6" lg="4">
              <FormGroup>
                <b><Label for="assignedDev">Assigned Developer</Label></b>
                <AssignDevInput selection={dev} setSelection={setDev} />
              </FormGroup>
            </Col>
          </Row>

          <Row>
            <Col md="6" lg="4">
              <FormGroup>
                <b><Label for="defectStatus">Status</Label></b>
                <Input
                  type="select"
                  name="defectStatus"
                  id="defectStatus"
                  value={status}
                  onChange={e => setStatus(e.target.value)}
                  innerRef={register({ required: true })}
                >
                  <option value="OPEN">OPEN</option>
                  <option value="CLOSED">CLOSED</option>
                </Input>
                <p style={{ color: "red" }}>{errors.defectStatus && "Status is required."}</p>
              </FormGroup>
            </Col>

            <Col md="6" lg="4">
              <FormGroup>
                <b><Label for="priority">Priority</Label></b>
                <Input
                  type="select"
                  name="priority"
                  id="priority"
                  value={priority}
                  onChange={e => setPriority(e.target.value)}
                >
                  <option value="">None</option>
                  <option value="Low">Low</option>
                  <option value="Medium">Medium</option>
                  <option value="High">High</option>
                  <option value="Immediate">Immediate</option>
                </Input>
              </FormGroup>
            </Col>

            <Col md="12" lg="4">
              <FormGroup>
                <b><Label for="projectForDefect">Associated Project</Label></b>
                <Input
                  type="text"
                  disabled
                  name="projectForDefect"
                  id="projectForDefect"
                  value={projectTitle}
                />
              </FormGroup>
            </Col>
          </Row>

          <Row>
            <Col>
              {/* required input here */}
              <FormGroup>
                <b><Label for="dateIdentified">Date Identified</Label></b>
                <DateTimePicker
                  value={dateIdentified}
                  onChange={value => setDateIdentified(value)}
                  format="MM/DD/YYYY"
                  time={false}
                />
              </FormGroup>
            </Col>

            <Col md="6" lg="4">
              <FormGroup>
                <b><Label for="defectTargetDate">Target Resolution Date</Label></b>
                <DateTimePicker
                  value={new Date(targetResDate)}
                  onChange={value => onDateChange(value, setTargetResDate)}
                  format="MM/DD/YYYY"
                  time={false}
                />
              </FormGroup>
            </Col>

            <Col md="12" lg="4">
              <FormGroup>
                <b><Label for="defectEndDate">Actual Resolution Date</Label></b>
                <DateTimePicker
                  value={new Date(actualResDate)}
                  onChange={value => onDateChange(value, setActualResDate)}
                  format="MM/DD/YYYY"
                  time={false}
                />
              </FormGroup>
            </Col>
          </Row>

          <Row>
            <Col xs="12">
              <FormGroup>
                <b><Label for="defectDescription">Detailed Description</Label></b>
                <Input
                  type="textarea"
                  name="defectDescription"
                  id="defectDescription"
                  value={description}
                  onChange={e => setDescription(e.target.value)}
                />
              </FormGroup>
            </Col>

            <Col xs="12">
              <FormGroup>
                <b><Label for="progress">Progress</Label></b>
                <Input
                  type="textarea"
                  name="progress"
                  id="progress"
                  value={progress}
                  onChange={e => setProgress(e.target.value)}
                />
              </FormGroup>
            </Col>
          </Row>
        </ModalBody>

        <ModalFooter>
          <Button color="danger" onClick={handleClose}>Cancel</Button>
          <Button color="info" type="submit">Submit</Button>
        </ModalFooter>
      </form>
    </Modal>
  )

};

export default EditDefectForm;